
.name {
  font-size: larger;
}
.loader {
  position: relative;
  top: 15px;
  width: 44px;
  height: 8px;
  margin: 12px auto;
}
.dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background: #ccc;
  position: absolute;
}
.dot_1 {
  animation: animateDot1 1.5s linear infinite;
  left: 12px;
  background: #E2B630;
}
.dot_2 {
  animation: animateDot2 1.5s linear infinite;
  animation-delay: 0.5s;
  left: 24px;
  background: #3084E2;
}
.dot_3 {
  animation: animateDot3 1.5s linear infinite;
  left: 12px;
  background: #E2B630;
}
.dot_4 {
  animation: animateDot4 1.5s linear infinite;
  animation-delay: 0.5s;
  left: 24px;
  background: #3084E2;
}
@keyframes animateDot1 {
0% {
    transform: rotate(0deg) translateX(-12px);
}
25% {
    transform: rotate(180deg) translateX(-12px);
}
75% {
    transform: rotate(180deg) translateX(-12px);
}
100% {
    transform: rotate(360deg) translateX(-12px);
}
}
@keyframes animateDot2 {
0% {
    transform: rotate(0deg) translateX(-12px);
}
25% {
    transform: rotate(-180deg) translateX(-12px);
}
75% {
    transform: rotate(-180deg) translateX(-12px);
}
100% {
    transform: rotate(-360deg) translateX(-12px);
}
}
@keyframes animateDot3 {
0% {
    transform: rotate(0deg) translateX(12px);
}
25% {
    transform: rotate(180deg) translateX(12px);
}
75% {
    transform: rotate(180deg) translateX(12px);
}
100% {
    transform: rotate(360deg) translateX(12px);
}
}
@keyframes animateDot4 {
0% {
    transform: rotate(0deg) translateX(12px);
}
25% {
    transform: rotate(-180deg) translateX(12px);
}
75% {
    transform: rotate(-180deg) translateX(12px);
}
100% {
    transform: rotate(-360deg) translateX(12px);
}
}
#tuesday, #wednesday, #thursday, #sunday, #warcraft, #additional, #trail {
  display: inline-block;
  width: 75px;
  padding: 10px 10px;
  margin: 10px 10px;
  background-color: rgb(208, 62, 62);
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently not supported by any browser */
  cursor: pointer;
  transition: background-color .5s
}
#tuesday:hover, #wednesday:hover, #thursday:hover, #sunday:hover, #warcraft:hover, #additional:hover, #trail:hover {
  border: 1px solid #060606;
  padding: 9px 9px;
}
table, th, td {
  margin: auto;
  border-collapse: collapse;
  padding: 5px 5px;
}
.v_form {
  padding-bottom: 5px;
}
input {
  font-size: large;
  font-family: 'Farsan';
  background-color: #E6E6E6;
}
input[type=number] {
  width: 10% !important;
}
input[type=text], input[type=number] {
  text-align: center;
  border: none;
  width: 50%;
  padding: 5px 5px;
  margin: 8px 0;
  box-sizing: border-box;
  border-bottom: 1px solid #ff8000;
}
input[type=text]:focus, input[type=number]:focus, select:focus {
  outline: none;
  border-bottom: 2px solid #ff8000;
}
select {
  width: 50%;
  border: none;
  border-bottom: 1px solid #ff8000;
  font-family: 'Farsan';
  font-size: large;
}
textarea {
  width: 75%;
  height: 99px;
  margin: 12px 20px;
  border: none;
  border-bottom: 1px solid #ff8000;
  resize: none;
  font-family: 'Farsan';
  font-size: large;
  background-color: #E6E6E6;
}
textarea:focus {
  outline: none;
  border-bottom: 2px solid #ff8000;
}
.v_form_submit {
  background-color: #ff9000;
  border: none;
  color: black;
  padding: 16px 32px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
  font-family: 'Farsan';
  font-size: large;
}
.v_form_submit:focus {
  outline: 1px solid #000000;
}
.v_form_submit:active {
  outline: 1px solid #0f0f0f;
}
#submitted {
  color: #3CB600;
}



